@charset "utf-8";

/* 입력폼 서식 */
.form {
  display: flex;
  padding: 1rem;
}

.form input {
  flex: 1;
  /* 버튼을 뺸 빈 공간을 모두 채워줍니다 */
  font-size: 1.25rem;
  outline: none;
  border: none;
  border-bottom: 1px solid #c5f6fa;
}

/* 추가(입력)버튼 서식 */
.form .create_btn {
  padding: 0.5rem 1rem;
  background: #22b8cf;
  border-radius: 3px;
  color: #fff;
  font-weight: 600;
  border: none;
  cursor: pointer;
}

.form .create_btn:hover {
  background: #3bc9bd;
}