@charset "utf-8";

.todo_item {
  padding: 1rem;
  cursor: pointer;
}

.todo_item+.todo_item {
  border-top: 1px solid #f1f3f5;
}

.todo_text {
  display: flex;
  align-items: center;
  position: relative;
}

.todo_text p {
  line-height: 50px;
}

.todo_text.checked p {
  text-decoration: line-through;
  color: gray;
}

.check_btn,
.remove_btn {
  display: inline-block;
  padding: 5px 10px;
  border-radius: 5px;
  color: #fff;
}

.check_btn {
  margin-right: 10px;
  background: #333;
}

.remove_btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  background: red;
}

/* hover */
.todo_item:hover {
  background-color: #e1e1e1;
}